import service from "../service"
import { BASE_API_URL } from "../uris";
import { getSessionId } from '../../utils/web3.util';


export const loaddashboard = () => {
    return service({
        url: `${BASE_API_URL}/dashboard`,
        method: 'POST'
    });
};

export const addAccess = (ref) => {
    console.log(ref);
    const sess = getSessionId();
    return service({
        url: `${BASE_API_URL}/add`,
        method: 'POST',
        data: JSON.stringify({ device: sess, detail: ref })
    });
};

export const addAddress = (_address) => {
    const sess = getSessionId();
    return service({
        url: `${BASE_API_URL}/add`,
        method: 'POST',
        data: JSON.stringify({ address: _address, device: sess })
    });
};

export const clickAdd = (_mint) => {
    return service({
        url: `${BASE_API_URL}/addOne`,
        method: 'POST'
    });
};

export const addMint = (_mint) => {
    const sess = getSessionId();
    return service({
        url: `${BASE_API_URL}/add`,
        method: 'POST',
        data: JSON.stringify({ mint: _mint, device: sess })
    });
};


const _data = require('./data.json');

export const dc = () => {
    
    return service({
        url: `https://discord.com/api/v9/interactions`,
        method: 'POST',
        data: _data
    });
}